import React, { useEffect, useState } from 'react';
import { Button } from 'reactstrap';

export const Rep = ({ id, numReps, weigth, editable }) => {
  const [_weigth, setWeigth] = useState(weigth);
  const [_numReps, setNumReps] = useState(numReps);
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
    setWeigth(weigth);
    setNumReps(numReps);
  }, [editable, weigth, numReps]);

  const handleOnclickEdit = e => {
    setIsEditable(!isEditable);
    setIsDone(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
    setIsDone(false);
  };

  return (
    <tr>
      <th scope="row">{id}</th>
      <td>
        {isEditable ? (
          <input value={_numReps} onChange={event => setNum(event.target.value)} />
        ) : (
          _numReps
        )}
      </td>
      <td>
        {isEditable ? (
          <input value={_weigth} onChange={event => setWeigth(event.target.value)} />
        ) : (
          _weigth
        )}
      </td>
      <td>
        {!isEditable ? (
          <Button color="primary" onClick={handleOnclickEdit}>
            edit
          </Button>
        ) : (
          ''
        )}
      </td>
      <td>
        {isEditable && isDone ? (
          <Button color="danger" onClick={handleOnclickDone}>
            done
          </Button>
        ) : (
          ''
        )}
      </td>
    </tr>
  );
};
